footer nav a {
  color: lightblue;
  margin: 0 10px;
  text-decoration: none;
  bottom: 0;
}

footer nav a:hover {
  text-decoration: underline;
}
.back-to-top {
  position: fixed;
  bottom: 1.5rem;
  right: 1.5rem;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 1.2rem;
  cursor: pointer;
}

.back-to-top:hover {
  background-color: #45a049;
}
