.about {
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about h2 {
  font-size: 2rem;
  color: #333;
  margin-bottom: 1.5rem;
}

.about p {
  font-size: 1rem;
  line-height: 1.6;
  color: #555;
  margin-bottom: 1rem;
}

.about ul {
  list-style-type: disc;
  margin-left: 2rem;
  color: #555;
}

.about a {
  color: #8561fb;
  text-decoration: none;
  font-weight: bold;
}

.about a:hover {
  text-decoration: underline;
}
