nav {
  background-color: #282c3447;
  padding: 1rem;
  display: flex;
  justify-content: center;
  gap: 20px;
}

nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: bold;
  padding: 0.5rem;
  border-radius: 5px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

nav a:hover {
  background-color: #444;
}

nav a.active {
  background-color: #67666c;
  color: black;
}

.one {
  background-color: #888495;
  color: black;
  transition: background-color 0.3s ease, color 0.3s ease;
}
