/* Home.css */

.home {
    font-family: Arial, sans-serif;
    color: #333;
  }
  
  .home main {
    padding: 2rem;
    text-align: center;
  }
  
  .home h2 {
    color: #4a2c2a; /* A warm, textile-themed color */
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  
  .home p {
    font-size: 1.2rem;
    color: #555;
    line-height: 1.8;
    margin: 1rem 0;
  }
  
  .highlights {
    margin: 2rem auto;
    padding: 1rem;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 10px;
  }
  
  .highlights h3 {
    color: #333;
    font-size: 1.8rem;
    margin-bottom: 1rem;
  }
  
  .highlights ul {
    list-style-type: disc;
    margin: 0 auto;
    padding: 0 1.5rem;
    text-align: left;
  }
  
  .highlights li {
    margin: 0.5rem 0;
    font-size: 1rem;
  }
  
  footer {
    text-align: center;
    padding: 1rem;
    background-color: #f1f1f1;
  }
  